import { FC } from "react";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { addCatPost, selectCatPost } from "../../features/Post/postCatSlice";
import { selectPosts } from "../../features/Post/PostSlice";
import { ICatagories } from "../../interface/post.interface";

const IconGridItem: FC<{ cat: ICatagories }> = ({ cat }) => {
  const { posts } = useAppSelector(selectPosts);
  const postcat = useAppSelector(selectCatPost);
  const dispatch = useAppDispatch();

  return (
    <div
      className={`col d-flex align-items-start ${
        cat === postcat.cat ? "bg-secondary" : ""
      }`}
      onClick={() =>
        dispatch(
          addCatPost({
            posts:
              cat === "All"
                ? posts
                : posts.filter((item) => item.catagories === cat) ?? [],
            cat,
          })
        )
      }
    >
      <svg
        className="bi  text-muted flex-shrink-0 me-3"
        width="1.75em"
        height="1.75em"
      >
        <path d="M9.405 1.05c-.413-1.4-2.397-1.4-2.81 0l-.1.34a1.464 1.464 0 0 1-2.105.872l-.31-.17c-1.283-.698-2.686.705-1.987 1.987l.169.311c.446.82.023 1.841-.872 2.105l-.34.1c-1.4.413-1.4 2.397 0 2.81l.34.1a1.464 1.464 0 0 1 .872 2.105l-.17.31c-.698 1.283.705 2.686 1.987 1.987l.311-.169a1.464 1.464 0 0 1 2.105.872l.1.34c.413 1.4 2.397 1.4 2.81 0l.1-.34a1.464 1.464 0 0 1 2.105-.872l.31.17c1.283.698 2.686-.705 1.987-1.987l-.169-.311a1.464 1.464 0 0 1 .872-2.105l.34-.1c1.4-.413 1.4-2.397 0-2.81l-.34-.1a1.464 1.464 0 0 1-.872-2.105l.17-.31c.698-1.283-.705-2.686-1.987-1.987l-.311.169a1.464 1.464 0 0 1-2.105-.872l-.1-.34zM8 10.93a2.929 2.929 0 1 1 0-5.86 2.929 2.929 0 0 1 0 5.858z" />
      </svg>
      <div>
        <h4 className="fw-bold mb-0">{cat}</h4>
        <p>Paragraph of text beneath the heading to explain the heading.</p>
      </div>
    </div>
  );
};

export default IconGridItem;
