export const recentPostVarient: Variants = {
  initial: {
    x: -100,
    y: -100,
    opacity: 0,
  },
  animate: {
    x: 0,
    y: 0,
    opacity: 1,
    transition: {
      duration: 1,
      type: "spring",
    },
  },
};

export const RecentPost: FC<{
  title: string;
  posts: IPost[];
}> = ({ title, posts }) => {
  return (
    <motion.div
      className="bg-success bg-opacity-10 my-5"
      variants={staggerContainer}
      whileInView="animate"
      initial="initial"
    >
      <div className="container px-4 py-5" id="custom-cards">
        <h2 className="pb-2 border-bottom">{title}</h2>
        <Row>
          {posts.length < 3
            ? posts.map((item, idx) => {
                return <PostItemCard key={idx} {...item} />;
              })
            : posts
                .slice(0, 3)
                .map((item, idx) => <PostItemCard key={idx} {...item} />)}
        </Row>
      </div>
    </motion.div>
  );
};
