import { Spinner, Alert } from "react-bootstrap";
import { useAppSelector } from "../app/hooks";
import { AboutItem, RecentPost } from "../components/About/About";
import Caroslider from "../components/caroSlider/Caroslider";
import Contact from "../components/Contact/Contact";
import Footer from "../components/Footer/Footer";
import Header from "../components/Header/Header";
import { selectPosts } from "../features/Post/PostSlice";

const HomePage = () => {
  const { error, posts, status } = useAppSelector(selectPosts);
  return (
    <div>
      <Header />
      <AboutItem />
      {status === "loading" ? (
        <>
          <Spinner animation="border" />
        </>
      ) : (
        !error && <RecentPost title="Recent Posts" posts={posts.slice(0, 3)} />
      )}

      <Caroslider />

      {error ? (
        <>
          {" "}
          <Alert>{error}</Alert>
        </>
      ) : status === "loading" ? (
        <>
          <Spinner animation="border" />
        </>
      ) : (
        <RecentPost
          title="Police Activity"
          posts={posts.filter((p) => p.catagories === "PoliceActivity")}
        />
      )}

      <Contact />
      <Footer />
    </div>
  );
};

export default HomePage;
