import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import { ICatagories, IPost } from "../../interface/post.interface";

export interface PostState {
  posts: IPost[];
  cat: ICatagories;
}

const initialState: PostState = {
  posts: [],
  cat: "All",
};

const postCatSlice = createSlice({
  name: "postCatSlice/slice",

  initialState,
  reducers: {
    addCatPost: (state, action: PayloadAction<PostState>) => {
      state.posts = action.payload.posts;
      state.cat = action.payload.cat;
    },
  },
});

export const { addCatPost } = postCatSlice.actions;

export const selectCatPost = (state: RootState) => state.postCat;
export default postCatSlice.reducer;
