import { motion } from "framer-motion";
// import { recentPostVarient } from "../About/About";
// import { fromLeftFade, staggerContainer } from "../Header/Header";
// import { Link } from "react-router-dom";
// import { fromUpFade } from "../Head/MyNavbar/AppBar";
import PostItem from "./PostItemCard";
import IconGridItem from "./IconGridItem";
import { staggerContainer } from "../Header/Header";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { selectPosts } from "../../features/Post/PostSlice";
import { Alert } from "react-bootstrap";
import { ALLCATAGORIES } from "../../interface/post.interface";
import { addCatPost, selectCatPost } from "../../features/Post/postCatSlice";
import { useEffect } from "react";

const PostCase = () => {
  const { posts, error, status } = useAppSelector(selectPosts);
  const postcat = useAppSelector(selectCatPost);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(addCatPost({ posts, cat: "All" }));
    return () => {};
  }, [dispatch, posts]);
  return (
    <div>
      <motion.main
        variants={staggerContainer}
        initial="initial"
        whileInView="animate"
      >
        <div className="container py-4">
          <div className="p-5 mb-4 bg-light rounded-3">
            <div className="container-fluid py-5">
              <h1 className="display-5 fw-bold">Custom jumbotron</h1>
              <p className="col-md-8 fs-4">
                Using a series of utilities, you can create this jumbotron, just
                like the one in previous versions of Bootstrap. Check out the
                examples below for how you can remix and restyle it to your
                liking.
              </p>
              <button className="btn btn-primary btn-lg" type="button">
                Example button
              </button>

              <section>
                <div className="container px-4 py-5">
                  <h2 className="pb-2 border-bottom">Catagories</h2>
                  <div
                    className=" row row-cols-1 row-cols-sm-2 row-cols-md-2 row-cols-lg-3 g-4 
                   py-5"
                  >
                    {ALLCATAGORIES.map((i, idx) => (
                      <IconGridItem key={idx} cat={i} />
                    ))}
                  </div>
                </div>
              </section>
            </div>
          </div>
        </div>
        <div className="album py-5 bg-light">
          <div className="container">
            <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-3 g-3">
              {status === "loading" && <div>Loading...</div>}
              {error && <Alert>{error}</Alert>}
              {postcat.posts.length > 0 ? (
                postcat.posts.map((post, id) => <PostItem key={id} {...post} />)
              ) : (
                <p className="display-3 fw-bold">No post found </p>
              )}
            </div>
          </div>
        </div>
      </motion.main>
    </div>
  );
};

export default PostCase;

